<template>
  <div id="orderList">
    <div id="orderDesc">
      <div class="fs-3">訂購資訊</div>
      <div class="py-3">
        <div class="fs-4"><span class="text-danger">✽</span> 取餐方式</div>
        <div class="my-1">
          <span class="m-2">
            <input type="radio" id="bySelf" class="btn-check" name="getStyle" />
            <label
              class="btn btn-outline-warning text-dark optionBtn optionBtn"
              for="bySelf"
              >自取
            </label>
          </span>
          <span class="m-2">
            <input type="radio" id="byShop" class="btn-check" name="getStyle" />
            <label
              class="btn btn-outline-warning text-dark optionBtn optionBtn"
              for="byShop"
              >外送
            </label>
          </span>
        </div>
      </div>
      <div class="py-3 border-top">
        <div class="fs-4"><span class="text-danger">✽</span> 聯絡人資訊</div>
        <label class="fs-5 my-1" for="buyerName">姓名</label>
        <input
          class="form-control form-control-sm optionText my-1"
          type="text"
          id="buyerName"
        />
        <label class="fs-5 my-1" for="buyerPhone">電話</label>
        <input
          class="form-control form-control-sm optionText my-1"
          type="text"
          id="buyerPhone"
        />
      </div>
      <div class="py-2 border-top">
        <div class="fs-4"><span class="text-danger">✽</span> 結帳方式</div>
        <div class="my-1">
          <input type="radio" id="byCash" class="btn-check" />
          <label
            class="btn btn-outline-warning text-dark optionBtn optionBtn"
            for="byCash"
            >現金
          </label>
        </div>
      </div>
      <div class="py-3 border-top">
        <div class="fs-4">
          訂購明細 <span class="fs-6"></span>
          <!-- (總金額:100元) -->
        </div>
        <div class="px-2 my-1" v-for="temp in orderData" :key="temp.id">
          <span class="fs-5">{{temp.productName}}</span><span></span><br />
          <span>{{temp.whatCup}} / {{temp.whatTemp}} / {{temp.whatSugar}} / ${{temp.totalPrice}} / {{temp.orderNum}}杯</span>
        </div>
      </div>
      <div class="py-3 border-top d-flex justify-content-end">
        <span class="m-1">
          <router-link to="/"
            ><button class="btn btn-outline-secondary optionBtn2">
              繼續加購
            </button>
          </router-link>
        </span>
        <span class="m-1">
          <router-link to="/"
            ><button class="btn btn-outline-secondary optionBtn2">結帳</button>
          </router-link>
        </span>
      </div>
    </div>
  </div>
</template>

<script>
import { reactive } from "vue";
export default {
  computed: {
    orderData() {
      return this.$store.state.userOrder;
    },
  },
  setup() {
    let orderInfo = reactive({
      orderList:''
    });
    return { orderInfo };
  },
};
</script>

<style lang="scss" scoped>
#orderList {
  width: 50%;
  margin: 0 auto;
  padding: 40px 0px;
  @media (max-width: $largeSizeView) {
    width: 70%;
  }
  @media (max-width: $mediumSizeView) {
    width: 95%;
  }
  #orderDesc {
    padding: 20px 15px;
    border-radius: 0.5rem;
    background: white;
    box-shadow: 0px 5px 20px -2px rgba(116, 116, 116, 0.6);
    .optionBtn {
      width: 100px;
    }
    .optionBtn2 {
      height: 50px;
      width: 120px;
    }
    .optionText {
      width: 300px;
      @media (max-width: $mediumSizeView) {
        width: 100%;
      }
    }
  }
}
</style>